
.cake,
.cake img{
  max-width: 100%;
}

.cake{
  position: absolute;
  top: -80%;
  left: 20%;
  width: 65%;
  transform: translate(0, 100%);
}

.cake img{
    margin: auto;
    display: block;
}

.flame {
  width: 45px;
  position: absolute;
  left: 50%;
  transform: translateX(-60%);
  top: -25px;
  opacity: .9;
  transition: opacity 0.5s ease-in-out;
}

.extinguished{
  opacity: 0;
}